}
.title {
  position: absolute;
  top: 100px;
  width: 100vw;
  z-index: 4;
  background-color: white;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 20px;
  font-size: 18px;
  box-shadow: 0 0 10px -6px #333;
}

img.splash {
  z-index: 3;
  min-height: 100vh;
  min-width: 100vw;
  position: absolute;
  top: calc(50% + 100px);
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  box-shadow: 0 0 16px -8px #333;
}

.name {
  position: absolute;
  top: 50%;
  width: 100%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  z-index: 4;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
