  display: none;
}

@media (min-width: 1024px) {
  article {
    padding: 120px 15vw;
    position: static;
  }

  .overview {
    padding-top: 220px;
  }

  div.wrapOverview {
    height: 40vw;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(3, 1fr);
    justify-items: stretch;
    -webkit-align-items: stretch;
            align-items: stretch;
    grid-auto-rows: minmax(50px, auto);
  }

  .title {
    grid-column: 1/3;
    grid-row: 1/2;
    -webkit-transform: translate(-20%, -20%);
            transform: translate(-20%, -20%);
    padding: 50px 20px;
    font-size: 18px;
    position: static;
    top: 0;
    width: unset;
  }

  img.splash {
    grid-column: 1/6;
    grid-row: 1/4;
    justify-self: stretch;
    position: static;
    min-height: unset;
    min-width: unset;
    height: unset;
    width: unset;
    z-index: 0;
    -webkit-transform: none;
            transform: none;
  }
  .name {
    grid-column: 2/5;
    grid-row: 2/3;
    z-index: 2;
    letter-spacing: 8px;
    font-size: 100px;
    position: static;
    -webkit-transform: none;
            transform: none;
    width: unset;
  }

  .blurb {
    z-index: 2;
    background-color: white;
    grid-column: 4/6;
    grid-row: 3/4;
    font-size: 14px;
    padding: 20px;
    -webkit-transform: translate(20%, 20%);
            transform: translate(20%, 20%);
    height: unset;
    width: unset;
    display: block;
    position: static;
    z-index: 4;
    background-color: white;
    box-shadow: 0 0 10px -6px #333;
  }
