.menu__logo {
  width: 100%;
  height: 30px;
  line-height: 30px;
  padding-left: 20px;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: bold;
  letter-spacing: 2px;
  background-color: #ddd;
  color: #0984e3;
  position: absolute;
  bottom: 0;
  left: 0;
}

.mainMenu a {
  font-size: 16px;
  /* border: 3px solid black; */
  /* box-shadow: 0 0 0 3px black; */
  width: 80%;
  height: 50px;
  line-height: 50px;
  position: relative;
  transition: 0.2s;
  text-align: center;
  z-index: 6;
  color: black;
  transition: 0.2s;
  font-weight: normal;
}

.mainMenu a::after {
  display: block;
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: white;
  right: 10px;
  top: 10px;
  z-index: -3;
  transition: 0.2s;
}

.mainMenu a::before {
  display: block;
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: transparent;
  top: 0;
  left: 0;
  border: 2px solid #ddd;
  z-index: -2;
  box-sizing: border-box;
  transition: 0.2s;
}

.mainMenu a.active {
  text-shadow: 0px 0px 1px black;
}

